/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: 51px;
    height: 51px;
    margin: 0;
    padding: 0 ;
    color: #fff;
    font-size: 14px;
    line-height: 49px;
    letter-spacing: .03em;
    font-family: $default-heading-font;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    border-radius: 50%;
    background-color: #ee9445;
    @include eltdfTransform(translateZ(0));
}

@mixin eltdfWooCommerceSaleStyle() {
    left: 15px;
    top: 15px;
    background-color: #ee9445;
}

@mixin eltdfWooCommerceOutOfStockStyle() {
    left: 15px;
    top: 15px;
    background-color: #ee9445;
}

@mixin eltdfWooCommerceNewProductStyle() {
    left: 15px;
    top: 15px;
    background-color: #ee9445;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: #434343;
    font-size: 15px;
    letter-spacing: .06em;
    font-weight: 300;
    font-family: $default-heading-font;

    .woocommerce-Price-currencySymbol {
        font-size: 9px;
        top: -3px;
        position: relative;
    }
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "linea-weather-10" !important;
    font-size: 12px;
    line-height: 1;
    letter-spacing: 2px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    letter-spacing: 8px;
    
    &:before {
        content: "\e032\e032\e032\e032\e032";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e032\e032\e032\e032\e032";
            color: $first-main-color;
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e032";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e032\e032\e032\e032\e032";
        color: $first-main-color;
        position: absolute;
        right: 1px;
        bottom: 8px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */