/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-cart-icon {
                font-size: 18px;
                line-height: 1em;
                color: $default-heading-color;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
    
                .eltdf-cart-number {
                    display: inline-block;
                    width: 13px;
                    height: 13px;
                    line-height: 13px;
                    border-radius: 13px;
                    background-color: $first-main-color;
                    font-size: 10px;
                    color: #fff;
                    text-align: center;
                    position: absolute;
                    right: -6px;
                    bottom: -4px;
                    font-family: 'Arial';
                }
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
    
                .eltdf-cart-number {
                    display: inline-block;
                    width: 13px;
                    height: 13px;
                    line-height: 13px;
                    border-radius: 13px;
                    background-color: $first-main-color;
                    font-size: 10px;
                    color: #fff;
                    text-align: center;
                    position: absolute;
                    right: -8px;
                    bottom: -6px;
                }
            }
        }
    }

    &:hover {

        .eltdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 335px;
    background-color: #000;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include eltdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 42px 32px 42px 43px;
        padding: 0;

        li {
            @include eltdfTableLayout();
            padding: 16px 0;
            box-sizing: border-box;

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
                border-top: 1px solid #393939;
            }
    
            &.eltdf-empty-cart {
                border-top: 0;
                color: #fff;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 78px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .eltdf-item-info-holder {
        @include eltdfTableCellLayout();
        vertical-align: top;
        padding-left: 20px;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            color: #fff;
            text-transform: uppercase;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;
            color: #fff;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .eltdf-quantity {
            display: block;
            margin: 3px 0 0;
            color: #fff;
        }

        .amount {
            display: block;
            margin: 3px 0 0;
            color: #fff;
            .woocommerce-Price-currencySymbol {
                font-size: 9px;
                vertical-align: top;
                margin-top: -1px;
                margin-right: 1px;
                display: inline-block;
            }
        }
    }

    .eltdf-cart-bottom {
        padding: 27px 0 0;
        margin-top: 17px;

        .eltdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 25px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: #fff;
                line-height: 20px;
                text-transform: uppercase;
                font-size: 16px;
                font-weight: 300;
    
            }

            .eltdf-total-amount {
                text-align: right;
	            font-size: 16px;
            }
        }

        .eltdf-view-cart {
            @include eltdfRelativeHolderLayout();
            height: 53px;
            line-height: 53px;
            padding: 0 26px;
            font-size: 13px;
            font-family: $default-heading-font;
            font-weight: inherit;
	        letter-spacing: 1px;
            color: #000;
            background-color: #fff;
            text-align: center;
            box-sizing: border-box;
            text-transform: uppercase;
            @include eltdfTransition(color .2s ease-out, background-color .2s ease-out, border-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $first-main-color;
            }
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */