/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.eltdf-woo-pagination-holder {
    @include eltdfRelativeHolderLayout();
    margin: 0 0 66px;
    clear: both;

    .eltdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: center;

    .page-numbers {
        position: relative;
        display: inline-block;
        width: auto;
        padding: 0;
        vertical-align: top;
        margin: 0 2px;
        font-size: 18px;
        font-weight: 300;
        line-height: 55px;
        text-decoration: none;
        cursor: pointer;
        white-space: nowrap;
        color: $default-heading-color;
        @include eltdfTransition(color .2s ease-out);
        font-family: $default-heading-font;
    
        &:hover,
        &.current {
            color: $first-main-color;
        }

        &.next,
        &.prev {
            font-size: 26px;
            display: inline-block;
            vertical-align: middle;
            height: 55px;
            width: 55px;
            border: 1px solid #000;
            line-height: 55px;
            color: $default-heading-color;

            &:before {
                position: relative;
                top: -1px;
                display: block;
                font-family: 'linea-arrows-10';
                font-size: 18px;
                line-height: inherit;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
    
            &:hover {
                color: $default-heading-color;
            }
        }

        &.prev {
            float: left;
            &:before {
                content: "\e03f";
            }
        }

        &.next {
            float: right;
            &:before {
                content: "\e04b";
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */